<template>
  <div class="container">
    <div class="row row-cols-1 row-cols-md-3 g-4">
      <div v-for="(movie, index) in movies" :key="movie.id" class="col">
        <!-- 卡片错位效果：根据索引偏移不同的垂直位移 -->
        <div
            class="card shadow card-hover-effect h-100"
            :style="{ transform: `translateY(${(index % 3) * 60}px)` }"
        >
          <div class="ratio-2x3">
            <img
                :src="movie.posterUrl || placeholder"
                referrerpolicy="no-referrer"
                alt="电影海报"
            />
          </div>
          <div class="card-body">
            <h5 class="card-title">
              {{ movie.chineseName ? movie.chineseName + ' - ' + movie.name : movie.name }}
            </h5>
            <router-link
                :to="`/movie/${movie.id}`"
                class="btn btn-outline-primary btn-sm mt-2"
            >查看详情</router-link>
          </div>
        </div>
      </div>
    </div>
    <div v-if="total > size" class="mt-4 text-center">
      第 {{ page }} 页 / 共 {{ Math.ceil(total/size) }} 页
    </div>
  </div>
</template>

<script>
import http from '@/utils/http';
import placeholder from '@/assets/image/placeholder.jpg';

export default {
  data() {
    return {
      movies: [],
      placeholder,
      total: 0,
      page: 1,
      size: 24,
      keyword: ''
    };
  },
  watch: {
    '$route.query.keyword': {
      handler() { this.page = 1; this.fetchMovies(); },
      immediate: true
    }
  },
  methods: {
    async fetchMovies() {
      this.keyword = this.$route.query.keyword || '';
      try {
        const res = await http.get('/movies', {
          params: { page: this.page, keyword: this.keyword }
        });
        this.movies = res.data.movies;
        this.total  = res.data.total;
        this.size   = res.data.size;
      } catch (e) {
        alert('加载电影列表失败：' + (e.response?.data || e.message));
      }
    }
  },
  created() {
    this.fetchMovies();
  }
};
</script>
